import { Link } from "react-router-dom";
import useAuth from "./hooks/userAuth";

const items = [
  { name: "Get Started", link: "/Write" },
  { name: "ContactUs", link: "" },
  { name: "AboutUs", link: "" },
  { name: "Home", link: "/" },
];
function NavBar() {
  const { user, logout } = useAuth();
  return (
    <div className="flex justify-between mx-12 my-6">
      <div className="flex flex-row">
        {items.map((item, index) => (
          <Link to={item.link}>
            <div
              className={
                "mx-2 font-pop cursor-pointer text-gray-200" +
                (index === 0
                  ? "underline underline-offset-4 decoration-4 decoration-indigo-300"
                  : "")
              }
            >
              {item.name}
            </div>
          </Link>
        ))}
      </div>
      <div>
        <div className="mx-2 font-pop flex flex-row">
          {user ? user.email : <></>}
          <div onClick={logout} className="cursor-pointer">
            (Logout)
          </div>
        </div>
      </div>
    </div>
  );
}

export default NavBar;
